.App {
  text-align: center;
  color: white;
  background-color: #282c34;
  position: absolute;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 100%;
  padding-bottom: 3em;
}

.mia {
  background-color: #282c34;
}

.social {
  margin: 0 0 9px 0;
  display: block;
  flex-direction: row;
  text-align: center;
}
.social button {
  background-color: rgba(0,0,0,0);
  border-color: rgba(0,0,0,0);
}
.social button:hover {
  cursor: pointer;
}

.underwear{
  background-color: #282c34;
  min-width: 100%;
  height: 11%;
  min-height: 11%
}

a {
  color: white;
  text-decoration: none;
  font-style: bold;
}

svg {
  padding: 0 5px 0 5px;
  -webkit-filter: invert(100%);
  filter: invert(100%);
}
