
.aboutdiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.divider {
  margin-bottom: 2em;
}
.header {
  color: #f2c777;
}
.industry {
  margin: 1em;
  text-align: center;
  padding: 1em;
  cursor: pointer;
}
.industry h3 {
  margin: 0;
}
.industry h4 {
  margin: 0;
}
.industry h6 {
  margin: 0;
}
.past {
  align-content: flex-end;
}
.past h3 {
  color: white;
}
.past h4 {
  color: #bbb;
}
.past h6 {
  color: #bbb;
}
