.mia {
  background-color: #282c34;
}

.project {
  margin: 3px;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: white;
  border: 2px solid #f2c777;
}
.project a {
  align-items: flex-end;
  margin: 0;
  transition: .5s;
}
.project h3 {
  width: 100%;
  color: #f2c777;
  transition: .5s;
  background-color: #282c34;
  transform: translateY(6em);
}
.project h4 {
  opacity: 0;
  transition: .1s;
}
.project p {
  opacity: 0;
  transition: .2s;
}
.project:hover {
  border: 2px solid #fff;
  transition: .1s;
}
.project:hover a {
  color: #61dafb;
  transition: .1s;
}
.project:hover h3 {
  align-items: flex-start;
  transform: translateY(0);
  transition: .3s;
}
.project:hover h4 {
  opacity: 1;
  transition: .2s;
}
.project:hover p {
  opacity: 1;
  transition: .2s;
}
.projectdiv {
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1%;
  margin: 1em;
}

.underwear{
  background-color: #282c34;
  min-width: 100%;
  height: 11%;
  min-height: 11%
}

.unused {
  margin: 3px;
  background-color: #f2c777;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #f2c777;
}

.unused:hover {
  cursor: pointer;
}

.unused h1 {
  transform: translateY(-0.5em);
}

.unused a {
  color: black;
}

a {
  color: white;
  text-decoration: none;
  font-style: bold;
}

svg {
  padding: 0 5px 0 5px;
  -webkit-filter: invert(100%);
  filter: invert(100%);
}
